import { Component, inject, OnInit, TemplateRef } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { jsPDF } from 'jspdf';
import { ExportToExcelService } from 'src/app/core/services/common/export-to-excel.service';
import { CustomerService } from 'src/app/core/services/customer/customer.service';
import { PagerService } from 'src/app/core/services/pagination/pager.service';

@Component({
  selector: 'app-customer-bill',
  templateUrl: './customer-bill.component.html',
  styleUrls: ['./customer-bill.component.scss'],
})
export class CustomerBillComponent implements OnInit {
  arrCustomer: any = [];
  arrAllCustomers: any = [];
  // pagination
  pager: any = {};
  intTotalCount = 0;
  intPageLimit = 50;
  intSkipCount = 0;
  fetchLoading = false;

  customer: any = {};
  blnInvoiceHide = true;

  today = new Date();
  taxNumber = '104110608700003';

  myForm: FormGroup;
  private modalService = inject(NgbModal);

  constructor(
    private customerService: CustomerService,
    private formBuilder: FormBuilder,
    private exportToExcelService: ExportToExcelService,
    private pageServiceObj: PagerService
  ) {
    this.myForm = this.formBuilder.group({
      customer: [null],
      fromDate: [''],
      toDate: [''],
    });
  }

  ngOnInit() {
    this.getAllCustomer();
    this.getAllCustomerAll();
  }

  setPage(page) {
    if (page < 1 || page > this.pager.totalPages) {
      return;
    }
    this.pager = this.pageServiceObj.getPager(
      this.intTotalCount,
      page,
      this.intPageLimit
    );
    this.getAllCustomer();
  }

  getAllCustomerAll() {
    const obj = {
      strLoginUserId: localStorage.getItem('userId'),
      fkCustomerId: '',
      strName: '',
      cust_Code: '',
      fromDate: '',
      endDate: '',
      intSkipCount: '0',
      intPageLimit: '1000000',
    };
    this.customerService.getAllCustomer(obj).subscribe((res) => {
      this.fetchLoading = false;
      if (res.success) {
        this.arrAllCustomers = res.data.map((d) => {
          return {
            ...d,
            displayName: d.cust_Code + ' - ' + d.strName,
          };
        });
      } else {
        this.arrAllCustomers = [];
      }
    });
  }

  getFromCount() {
    return this.intPageLimit * (this.pager.currentPage - 1) + 1;
  }

  getToCount() {
    return (
      this.intPageLimit * (this.pager.currentPage - 1) + this.arrCustomer.length
    );
  }
  getAllCustomer() {
    let skipCount = this.intSkipCount;
    if (this.pager.intSkipCount) {
      skipCount = this.pager.intSkipCount;
    }
    const obj = {
      strLoginUserId: localStorage.getItem('userId'),
      intSkipCount: skipCount,
      intPageLimit: this.intPageLimit,
      fkCustomerId: '',
      cust_Code: this.myForm.value.customer,
      fromDate: this.myForm.value.fromDate,
      endDate: this.myForm.value.toDate,
    };

    this.fetchLoading = true;
    this.customerService.getAllCustomer(obj).subscribe((res) => {
      this.fetchLoading = false;
      if (res.success) {
        this.arrCustomer = res.data;

        this.intTotalCount = res.count;

        this.pager = this.pageServiceObj.getPager(
          this.intTotalCount,
          this.pager.currentPage,
          this.intPageLimit
        );
      } else {
        this.arrCustomer = [];
        // alert(res.message);
      }
    });
  }

  open(content: TemplateRef<any>, customer) {
    this.customer = customer;
    this.modalService.open(content, {
      ariaLabelledBy: 'modal-basic-title',
      size: 'lg',
    });
  }

  downloadPDF() {
    const pages = document.querySelector('.printSection') as HTMLElement;
    const doc = new jsPDF({
      unit: 'px',
      format: [595, 842],
    });

    doc.html(pages, {
      callback: (doc: jsPDF) => {
        doc.deletePage(doc.getNumberOfPages());
        doc.save('pdf-export');
      },
    });
  }

  // public convertToPDF(htmlContent) {
  // html2canvas(htmlContent).then((canvas) => {
  //   // Few necessary setting options
  //   var imgWidth = 210;
  //   // var pageHeight = 210;
  //   var imgHeight = (canvas.height * imgWidth) / canvas.width;
  //   // var heightLeft = imgHeight;

  //   const contentDataURL = canvas.toDataURL('image/png');
  //   let pdf = new jsPDF('p', 'mm', 'a4'); // A4 size page of PDF
  //   var position = 0;
  //   pdf.addImage(contentDataURL, 'PNG', 0, position, imgWidth, imgHeight);
  //   // pdf.addPage();
  //   pdf.save(this.customer.cust_Code + '.pdf'); // Generated PDF
  // });
  // }
  public convertToPDF(type) {
    this.blnInvoiceHide = type === 'bill' ? false : true;
    setTimeout(() => {
      let printArea = document.getElementById('printArea');
      if (printArea) {
        let originalBody = document.body;
        let bodyElement = document.createElement('body');
        bodyElement.appendChild(printArea);
        document.body = bodyElement;
        window.print();
        document.body = originalBody;
      }
      this.modalService.dismissAll();
      this.blnInvoiceHide = true;
    }, 100);
  }
}
