import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { PaymentVoucherComponent } from './payment-voucher/payment-voucher.component';
import { ReceiptVoucherComponent } from './receipt-voucher/receipt-voucher.component';
import { RouterModule } from '@angular/router';
import { CustomerBillComponent } from './customer-bill/customer-bill.component';
import { TranslateModule } from '@ngx-translate/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { InlineSVGModule } from 'ng-inline-svg-2';
import { NgSelectModule } from '@ng-select/ng-select';
import { ReceiptVoucherDetailsComponent } from './receipt-voucher-details/receipt-voucher-details.component';
import { PaymentVoucherDetailsComponent } from './payment-voucher-details/payment-voucher-details.component';

@NgModule({
  declarations: [
    CustomerBillComponent,
    PaymentVoucherComponent,
    ReceiptVoucherComponent,
    ReceiptVoucherDetailsComponent,
    PaymentVoucherDetailsComponent,
  ],
  imports: [
    CommonModule,
    TranslateModule,
    FormsModule,
    ReactiveFormsModule,
    InlineSVGModule,
    NgSelectModule,
    RouterModule.forChild([
      {
        path: 'customer-bill',
        component: CustomerBillComponent,
      },
      {
        path: 'payment-voucher',
        component: PaymentVoucherComponent,
      },
      {
        path: 'payment-voucher/details/:id/:type',
        component: PaymentVoucherDetailsComponent,
      },
      {
        path: 'receipt-voucher',
        component: ReceiptVoucherComponent,
      },
      {
        path: 'receipt-voucher/details/:id/:type',
        component: ReceiptVoucherDetailsComponent,
      },
    ]),
  ],
})
export class BillModule {}
